    // setMessages(JSON.parse(`[{"content":"Hola! soy tu asistente personal. En qu puedo ayudarte?","isUser":false},{"isUser":true,"content":"Comparar las polticas de cobertura de ASSA con las de PALIG."},{"isUser":false,"content":"<p>Para comparar las polticas de cobertura de ASSA y PALIG, he extrado informacin relevante de los documentos proporcionados. A continuacin, presento una tabla comparativa con los aspectos clave de las coberturas de ambas compaas:</p>\\n<pre><code class=\\"language-html\\">&lt;style&gt;\\n  .comparison-table {\\n    width: 100%;\\n    margin: 20px 0;\\n    border-collapse: collapse;\\n    border-spacing: 0;\\n    box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.1);\\n    font-family: 'Roboto', sans-serif;\\n    border-radius: 4px;\\n    overflow: hidden;\\n  }\\n\\n  .comparison-table th {\\n    background-color: #f2f2f2;\\n    font-weight: bold;\\n    padding: 16px;\\n    text-align: left;\\n    border-bottom: 1px solid rgba(224, 224, 224, 1);\\n  }\\n\\n  .comparison-table td {\\n    padding: 16px;\\n    text-align: left;\\n    border-bottom: 1px solid rgba(224, 224, 224, 1);\\n  }\\n&lt;/style&gt;\\n\\n&lt;div style=\\"margin: 20px;\\"&gt;\\n  &lt;h3&gt;Comparacin de Polticas de Cobertura: ASSA vs PALIG&lt;/h3&gt;\\n  &lt;table class=\\"comparison-table\\"&gt;\\n    &lt;thead&gt;\\n      &lt;tr&gt;\\n        &lt;th&gt;Aspecto&lt;/th&gt;\\n        &lt;th&gt;ASSA&lt;/th&gt;\\n        &lt;th&gt;PALIG&lt;/th&gt;\\n      &lt;/tr&gt;\\n    &lt;/thead&gt;\\n    &lt;tbody&gt;\\n      &lt;tr&gt;\\n        &lt;td&gt;Monto Mximo de Cobertura&lt;/td&gt;\\n        &lt;td&gt;Hasta B/. 50,000.00 por ao y B/. 250,000.00 vitalicio para condiciones congnitas&lt;/td&gt;\\n        &lt;td&gt;Hasta B/. 1,000,000.00 por ao y B/. 2,500,000.00 vitalicio&lt;/td&gt;\\n      &lt;/tr&gt;\\n      &lt;tr&gt;\\n        &lt;td&gt;Alcance Geogrfico&lt;/td&gt;\\n        &lt;td&gt;Local e Internacional&lt;/td&gt;\\n        &lt;td&gt;Local e Internacional&lt;/td&gt;\\n      &lt;/t